.page[status-bar-fill]>.page__content {
	top: 0px !important;
}

button.btn:focus {
	outline: none !important;
}

.btn-transparent {
	background-color: transparent;
	border-color: transparent;
}
div.loader-container {
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
	z-index: 9999999;
	background: rgba(0, 0, 0, 0.70);

	div.loader {
		position: absolute;
		width: 120px;
		height: 120px;
		top: 50%;
		left: 50%;
		margin-top: -60px;
		margin-left: -60px;
		background: url(../img/loader.png) no-repeat center center;
		background-size: contain;
	}
}
.color-red {
	color: red !important;
}
.about_bg {
	width: 100% !important;
	height: 30vh !important;
}
.topbar {
	margin-top: -18rem !important;
}
.leftarrow {
	position: fixed;
    top: 0;
	margin-top: 10px;
}
.color-black {
	color: black !important;
}
.color-header {
	color: #232b62;
}
.color-bg {
	background: #232b62;
}
.header {
	padding-top: 8px;
    position: relative;
    margin-left: auto;
    margin-right: auto;
	font-weight: bold;
	font-size: 17px;
}
img.logo {
	max-height: 60px;
	max-width: 100%;
}
.button-add {
	background-color: #ffffff;
	border: none;
	color: black;
	font-size: 16px;
	cursor: pointer;
	border-radius: 19px;
	position: fixed;
	top: 50px;
	right: 5px;
	margin-top: -37px;
	height: 9px;
	width: 60px;
	padding-bottom: 23px;
	padding-left: 7px;
	font-weight: bold;
}
.heading-info {
	font-weight: bold;
	text-align: center;
	font-size: 16px !important;
}

.navbar-alignment {
	justify-content: space-between;
	align-items: center;
	height: 3rem;

	.btn-sm {
		padding: .2rem .3rem !important;
	}
}